import React from "react"
import Typist from "react-typist"
import "react-typist/dist/Typist.css"
import styled from "styled-components"

const TypingWrapper = styled.div`
  display: inline-block;
  font-size: 48px;
  line-height: 48px;
  font-family: ${props => props.theme.fontFamily};

  .shadow {
    padding-right: 10px;
    opacity: 0;
  }

  .Typist {
    height: 40px;

    .Cursor {
      color: ${props => props.theme.colorTheme};
    }
  }
`

export const Typing = () => {
  const [running, updateRunning] = React.useState(true)
  // const longestSentence = ""
  React.useEffect(() => {
    if (running === false) updateRunning(true)
  }, [running])

  return (
    <TypingWrapper>
      {/* <div className="shadow">{longestSentence}</div> */}
      {running ? (
        <Typist
          onTypingDone={() => updateRunning(false)}
          cursor={{ blink: true }}
        >
          <Typist.Delay ms={1000} />
          <span></span>
          <Typist.Delay ms={1000} />
          <span></span>
          <Typist.Delay ms={200} />
          <span></span>
          <Typist.Delay ms={100} />
          <span> Trista</span>
          <Typist.Backspace count={7} delay={1000} />
          <Typist.Delay ms={500} />
          <span></span>
          <Typist.Backspace count={10} delay={5000} />
          <Typist.Delay ms={1000} />
        </Typist>
      ) : (
        ""
      )}
    </TypingWrapper>
  )
}
